const getters = {
  userinfo: state => state.user.userinfo,
  responseType: state => state.user.responseType,
  errorMsg: state => state.errorLog.errorMsg,
  errorCode: state => state.errorLog.errorCode,
  loading: state => state.app.loading,
  currentRouter: state => state.user.currentRouter,
  timingRun: state => state.user.timingRun,
  StimingRun: state => state.user.StimingRun,
  HupRun: state => state.user.HupRun,
  HdownRun: state => state.user.HdownRun,
  ThotRun: state => state.user.ThotRun,
  TShotRun: state => state.user.TShotRun,
  TcodeRun: state => state.user.TcodeRun,
  TScodeRun: state => state.user.TScodeRun,
  roomIcons: state => state.user.roomIcons,
  deviceName: state => state.user.deviceName,
  loginStatus: state => state.user.loginStatus,
  devicesData: state => state.user.devicesData,
  currentMac: state => state.user.currentMac,
  maqttState: state => state.user.maqttState,
  showAction: state => state.plug.showAction,
  firstdevicePage: state => state.user.firstdevicePage

}

export default getters



