const plug = {
  state: {
    name: '',
    isShow: false,
    messageBox: false,
    sideMask: false,
    showLogout: false, // logout
    showActionsheet: false, // actionSheet
    showAction: false, // actionSheet
    ToastType: null, // 消息提示框类型
    showUpdate: false, // Update,
    messageType: null,
    loading: false // 等待加载style
  },
  mutations: {
    SET_NAME: (state, name) => {
      state.name = name
    },
    showBar (state) {
      state.isShow = true
    },
    Hide_Message (state) {
      state.messageBox = false
      // state.sideMask = !state.sideMask
    },
    hideBar (state) {
      state.isShow = false
    },
    HIDE_SideBarmask (state) {
      state.sideMask = !state.sideMask
    },
    SHOW_Logout (state) {
      state.messageBox = !state.messageBox
    },
    SHOW_Update (state) {
      state.messageBox = !state.messageBox
      state.showUpdate = !state.showUpdate
    },
    SHOW_actionsheet (state) {
      state.showActionsheet = !state.showActionsheet
      state.showAction = !state.showAction
    },
    SHOW_MessageBox (state, obj) {
      state.messageBox = !state.messageBox
      console.log('messg2')
      console.log(obj.type)
      if (obj.type === 'null') {
        setTimeout(() => {
          state.messageType = obj.type
        }, 500)
      } else {
        state.messageType = obj.type
      }
      console.log(state.messageType)
    },
    SHOW_LOADING (state, s) {
      state.loading = s
    }
  },
  actions: {
    addErrorLog ({commit}, name) {
      commit('SET_NAME', name)
    },
    showSideBar ({commit}) {
      commit('showBar')
    },
    hideSideBar ({commit}) {
      commit('hideBar')
    },
    hideSideBarmask ({ commit }) {
      commit('HIDE_SideBarmask')
    },
    showLogout ({commit}) {
      commit('SHOW_Logout')
    },
    showUpdate ({commit}) {
      commit('SHOW_Update')
    },
    showActionsheet ({commit}) {
      commit('SHOW_actionsheet')
    },
    showMessagesBox ({commit}, obj) {
      commit('SHOW_MessageBox', obj)
    },
    showLoading ({commit, state, rootState}, s) {
      commit('SHOW_LOADING', s)
      if (s) {
        setTimeout(() => {
          if (state.loading) {
            state.loading = false
            rootState.ToastType = 'overtime'
          }
          clearTimeout()
        }, 20000)
      }
    }
  },
  getters: {
    isShowMethod (state) {
      return state.isShow
    },
    hideSideBarmask (state) {
      return state.sideMask
    },
    showLogout (state) {
      return state.showLogout
    },
    messageBox (state) {
      return state.messageBox
    },
    showUpdate (state) {
      return state.showUpdate
    },
    getActionsheet (state) {
      return state.showActionsheet
    },
    messageType (state) {
      return state.messageType
    },
    getLoading (state) {
      return state.loading
    }
  }
}

export default plug



