/**
 * 判断设备类型 EQUIPMENT_TYPE
 * @method navigator
 */

export const navigator = () => {
  var ua = window.navigator.userAgent.toLowerCase()
  if (ua.match(/MicroMessenger/i) === 'micromessenger') {
    return 'Wechat'
  } else {
    var u = window.navigator.userAgent
    if (u.indexOf('Android') > -1 || u.indexOf('Linux') > -1) {
      return 'Android'
    } else if (u.indexOf('iPhone') > -1 || u.indexOf('iPad') > -1) {
      return 'IOS'
    } else {
      return 'NULL'
    }
  }
}

/**
  * 时间转为秒
  * @param time 时间(00:00:00)
  * @returns {string} 时间戳（单位：秒）
  */
export const timeToSec = (time) => {
  let s = ''
  let hour = time.split(':')[0]
  let min = time.split(':')[1]
  let sec = time.split(':')[2]
  s = Number(hour * 3600) + Number(min * 60) + Number(sec)
  return s
}

/**
 * 本地存储localStorage
 * @method setStore
 * @param {string}      name      名称，必选
 * @param {string}      content   内容，必选
 */

export const setStore = (name, content) => {
  console.log(name);
  console.log(content)
  if (!name) {
    return
  }
  if (typeof content !== 'string') {
 

    content = JSON.stringify(content)
  }
  window.localStorage.setItem(name, content)
}

export const RemStore = (name) => {
  if (!name) {
    return
  }
  // console.log('cccc'+window.localStorage.name)
  window.localStorage.removeItem(name)
}
 
/**
 * 本地存储localStorage
 * @method getStore
 * @param {string}      name      名称，必选
 */
export const getStore = (name) => {
  if (!name) {
    return
  }
   return window.localStorage.getItem(name)
}

/**
 * 结束时间计算   getEndTime
 * @method getEndTime
 * @param {Array}      start      开始时间
 * @param {Array}      Length      结束时间
 */

export const getEndTime = (start, Length) => {
  console.log(start, Length)
  let [h, m] = [parseInt(start[0]), parseInt(start[1])]
  if (m + parseInt(Length[1]) > 59) {
    h++
    m = m + parseInt(Length[1]) - 60
  } else {
    m = m + parseInt(Length[1])
  }
  if (m < 10) {
    m = '0' + m
  }
  if (h + parseInt(Length[0]) < 10) {
    h = '0' + (h + parseInt(Length[0]))
  } else {
    h = h + parseInt(Length[0])
  }
  let endTime = [h, m].join(':')
  console.log('结束时间' + endTime)
  return endTime
}

/**
 * 结束时间计算   getTimeLength
 * @method getTimeLength
 * @param {Array}      start   开始时间
 * @param {Array}      end      结束时间
 */

export const getTimeLength = (start, end) => {
  console.log(start, end)
  let [h, m] = [parseInt(end[0]), parseInt(end[1])]
  if (m - parseInt(start[1]) < 0) {
    h--
    m = m - parseInt(start[1]) + 60
  } else {
    m = m - parseInt(start[1])
  }
  if (m < 10) {
    m = '0' + m
  }
  if ((h - parseInt(start[0])) < 10) {
    h = '0' + (h - parseInt(start[0]))
  } else {
    h = h - parseInt(start[0])
  }
  let timeLength = [h, m].join(':')
  console.log('结束时间' + timeLength)
  return timeLength
}



