import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      // redirect: '/Home/device/temperature'
      redirect: '/Home/device'
      // redirect: '/Home/me'
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/page/login/login'),
      children: [
        {
          name: 'forgotStep',
          path: '/login/forgotStep',
          component: () => import('@/page/login/forgotStep')
        },
        {
          name: 'registerNext',
          path: '/login/registerNext',
          component: () => import('@/page/login/registerNext')
        }
      ]
    },
    {
      path: '/Home',
      name: 'Home',
      component: () => import('@/page/home/index'),
      children: [
        {
          path: 'me',
          name: 'me',
          component: () => import('@/page/me/me'),
          children: [
            {
              path: 'aboutUs',
              name: 'aboutUs',
              component: () => import('@/page/me/aboutUS')
            },
            {
              path: 'suggestion',
              name: 'suggestion',
              component: () => import('@/page/me/suggestion')
            },
            {
              path: 'changename',
              name: 'changename',
              component: () => import('@/page/me/changename')
            },
            {
              path: 'phonebind',
              name: 'phonebind',
              component: () => import('@/page/me/phonebind')
            }
          ]
        },
        {
          path: 'device',
          name: 'device',
          component: () => import('@/page/device/device'),
          children: [
            {
              path: 'temperature',
              name: 'temperature',
              component: () => import('@/page/temperature/index')
            },
            {
              path: 'addsuccess',
              name: 'addsuccess',
              component: () => import('@/page/deviceList/addevice/adsuccess')
            }
          ]
        }
      ]
    }

  ]
})



