<template>
  <div class="loadingAlert">
    <transition name="delay">
      <div class="mask" v-if="getLoading"></div>
    </transition>
    <transition name="fade">
      <div class="load" v-if="getLoading">
        <span></span>
        <em></em>
        <strong>请稍后...</strong>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {}
  },
  computed: {
    ...mapGetters(['getLoading'])
  },
  mounted () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../styles/mixin.less';
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.loadingAlert{
  .mask{
    .fs;
    z-index: 9999;
    background: rgba(0, 0, 0, 0.5);
  }
  @loadHeight: 120px;
  .load{
    .wh(@loadHeight, @loadHeight);
    border-radius: 6px;
    background: rgba(0, 0, 0, 0.55);
    position: absolute;
    left: 50%;
    top: 44%;
    z-index: 9999;
    margin: -@loadHeight/2 0 0 -@loadHeight/2;
    @runHeight: 56px;
    span{
      .wh(@runHeight, @runHeight);
      display: block;
      border: 4px solid #8B8B8B;
      border-radius: 50%;
      position: absolute;
      left: 50%;
      top: 38%;
      margin: -@runHeight/2 0 0 -@runHeight/2;
    }
    em{
      .wh(@runHeight/2, @runHeight/2);
      display: block;
      border-top: 4px solid #FFF;
      border-left: 4px solid #FFF;
      border-top-left-radius: 100%;
      position: absolute;
      left: 38%;
      top: 26%;
      margin: -@runHeight/4 0 0 -@runHeight/4;
      animation: rotate 1s infinite;
      transform-origin: right bottom;
    }
    strong{
      .wh-l(100%, 20px);
      display: block;
      font-size: 15px;
      position: absolute;
      left: 0;
      bottom: 13%;
      color: #FFF;
    }
  }
}
.delay-leave-active{
  transition-delay: .2s;
}
.fade-enter-active,.fade-leave-active{
  transition: all .3s ease-out;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
</style>



