<template>
  <div class="errorCode">
    <toast :describe="errorMsg" v-if="ToastType==='publicToast'" position="bottom"></toast>
     <toast describe="Email or Password error, please try again." v-if="ToastType==='passwordError'" position="bottom"></toast>
    <toast :describe="email + ' is not registered, please register and log in!'"  v-if="ToastType==='notRegister'" position="bottom"></toast>
    <toast :describe="email + 'is not activated yet, please activate and  log in!'" v-if="ToastType==='notActive'" position="bottom"></toast>
    <toast describe= "undefined"  v-if="ToastType==='emailLoginFailed'" position="bottom"></toast>
    <toast describe= "Email format error, please enter the correct Email !"  v-if="ToastType==='formatError'" ></toast>
    <toast :describe= "'The email:' + signEmail + ' has been registered, please login directly.'"  v-if="ToastType==='registeredError'" position="bottom"></toast>
    <toast describe= "We have sent a confirmation email, please check!"  v-if="ToastType==='success'" position="bottom"></toast>
    <toast describe="网络连接失败、服务器异常、请求超时" v-if="ToastType==='networkError'" position="bottom"></toast>
    <toast describe="undefined!" v-if="ToastType==='emailForgotFailed'" position="bottom"></toast>
    <toast :describe="forgotEmail + ' is  not registered,please sign up first !'" v-if="ToastType==='notRegistered'" position="bottom"></toast>
    <toast :describe="$t('userPage.enterPassword')" v-if="ToastType==='inputOldPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.samePasswordError')" v-if="ToastType==='sameNewPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.passwordInconsistentError')" v-if="ToastType==='newPsdDifferError'" position="bottom"></toast>
    <toast :describe="$t('userPage.newlPassword')" v-if="ToastType==='limitPsdError'" position="bottom"></toast>

    <toast :describe="$t('userPage.originalPasswordError')" v-if="ToastType==='wrongPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.failedChangePassword')" v-if="ToastType==='changePsdFailedError'" position="bottom"></toast>
    <toast :describe="$t('userPage.successfullyChanged')" v-if="ToastType==='changePsdSuccessError'" position="bottom"></toast>
    <toast describe="Please try again for operation timeout!" v-if="ToastType==='overtime'" ></toast>
    <toast describe="短信验证码发送成功" v-if="ToastType==='getCodeSuccess'" ></toast>
    <toast describe="手机号或验证码错误！" v-if="ToastType==='errorPhoneorCode'" ></toast>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import toast from '../../components/common/toastError'
export default {
  name: 'errorCode',
  components: {
    toast
  },
  data () {
    return {}
  },
  computed: {
    ...mapState(['ToastType']),
    ...mapGetters(['errorMsg'])
  },
  watch: {
    ToastType (newVal) {
      if (newVal && newVal !== 'lowbattery') {
        console.log(newVal)
        setTimeout(() => {
          this.$store.state.ToastType = null
        }, 3000)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.errorCode{}
</style>



