/**
 * 备份时间目录查询返回          BackupTimeAndDirectoryResponse
 * @method                     BackupTimeAndDirectoryResponse
 * @param         {number}     time       时间戳（0 无备份时间）
 * @param         {string}     dir        备份存放目录
 * @param         {string}      mac       mac地址
 */
export const BackupTimeAndDirectoryResponse = (navigator, self) => {
  console.log('备份时间目录查询返回')
  switch (navigator) {
    case 'Android':
    case 'IOS':
      window.BackupTimeAndDirectoryResponse = function (mac, time, dir) {
        if (self.currentDevice.mac === mac) {
          if (time === 0) {
            self.date = ''
            self.backupdata = false
          } else {
            let d = new Date()
            d.setTime(time)
            self.date = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
            let m = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes()
            self.time = d.getHours() + ':' + m
            self.backupdata = true
          }
          self.dir = dir
        }
      }
      break
    default:
      break
  }
}

/**
 * 备份数据到手机          BackupDataResponse
 * @method                     BackupDataResponse
 * @param         {boolean}    result     备份成功失败
 * @param         {string}     mac        mac地址
 */
export const BackupDataResponse = (navigator, self) => {
  console.log('备份数据到手机返回')
  switch (navigator) {
    case 'Android':
    case 'IOS':
      window.BackupDataResponse = function (mac, result) {
        if (self.currentDevice.mac === mac) {
          if (result) {
            self.backupStatus = true
            BackupTimeAndDirectoryRequest(self.NAVIGATOR, self, self.currentDevice.mac)
          } else {
            self.backupStatus = false
            self.MESSAGEBOX_SWITCH({
              type: self.messageBoxType
            })
            self.MESSAGEBOX_SWITCH({
              type: 'backupFailed'
            })
          }
        }
      }
      break
    default:
      break
  }
}
