/**
 * 修改密码返回 User
 * @method changePasswordResponse
 * @param  {string}    navigator   设备类型，必选
 */

export const changePasswordResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.changePasswordResponse = function (result, errorCode) {
        if (result) {
          self.$store.state.ToastType = 'changePsdSuccessError'
          self.running = false
          setTimeout(() => {
            self.$router.go(-1)
          }, 1000)
        } else {
          if (errorCode !== '') {
            // self.$store.state.ToastType = 'wrongPsdError'
            self.$store.state.ToastType = 'changePsdFailedError'
          }
        }
      }
      break
  }
}

/**
 * 检查是否有版本更新返回 User
 * @method inspectVersionUpdateResponse
 * @param  {string}    navigator   设备类型，必选
 */

export const inspectVersionUpdateResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.inspectVersionUpdateResponse = function (result, errorCode, upgrade) {
        if (result) {
          if (!upgrade) {
            self.showMessagesBox({type: 'showUpdate'})
          } else {
            self.updateTip = true
            setTimeout(() => {
              self.updateTip = false
              clearTimeout()
            }, 1000)
          }
        }
      }
      break
  }
}

