/**
 * 修改用户信息返回 User
 * @method modifyUserInformationResponse
 * @param  {string}    navigator   设备类型，必选
 */

export const modifyUserInformationResponse = (navigator, self, page) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      window.modifyUserInformationResponse = function (result, data) {
        if (result) {
          if (page === 'suggest') {
            self.picArr[self.picIndex] = JSON.parse(data).headPic
            console.log(self.$refs.picture.children[self.picIndex].children)
            self.$refs.picture.children[self.picIndex].children[0].src = JSON.parse(data).headPic
          } else if (page === 'changeName') {
            self.$router.go(-1)
            let localLoginInfo = JSON.parse(getStore('Userinfo'))
            localLoginInfo.nickName = JSON.parse(data).nickName
            setStore('Userinfo', localLoginInfo)
          } else {
            let obj = JSON.parse(data)
            console.log('更换头像')
            self.$refs.headPic.src = obj.headPic
            self.showLoading(false)
            setStore('Userinfo', data)
          }
        }
      }
      break
  }
}

