/**
 * 查询用户信息返回 User
 * @method userInformationResponse
 * @param  {string}    navigator   设备类型，必选
 */

export const userInformationResponse = (navigator, self, page) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.userInformationResponse = function (result, data) {
        if (result) {
          setStore('Userinfo', data)
          if (page === 'me') {
            console.log('me page')
            self.$nextTick(() => {
              let localUserInfo = JSON.parse(getStore('Userinfo'))
              self.name = localUserInfo.nickName
              if (self.$refs.headPic) {
                self.$refs.headPic.src = localUserInfo.headPic
              }
              if (localUserInfo.mobile !== undefined && localStorage.mobile !== '') {
                self.phoneBind = '已绑定'
              } else {
                self.phoneBind = '未绑定'
              }
              localUserInfo.thirdInfos.forEach(ele => {
                if (ele.type === 10) {
                  console.log('微信绑定')
                  self.wechatBind = '已绑定'
                } else if (ele.type === 11) {
                  console.log('支付宝绑定')
                  self.alipayBind = '已绑定'
                }
              })
            })
          }
        }
      }
      break
  }
}

/**
 *
 * @method deviceHistoryReportDataResponse
 * @param  {string}    navigator   设备类型，必选
 */

export const deviceHistoryReportDataResponse = (navigator, self, mac, timers, data, page) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.deviceHistoryReportDataResponse = function (mac, timers, data) {
        if (self.currentDeviceMAC === mac) {
          self.allPower = ((JSON.parse(data)[0].e) / 1000).toFixed(3)
        }
      }
      break
  }
}

/**
 * 查询设备历史数据返回 State
 * @method deviceHistoryDataResponse
 * @param  {string}    navigator   设备类型，必选
 */

export const deviceHistoryDataResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      window.deviceHistoryDataResponse = function (mac, startTime, totalDay, interval, data) {
        console.log(data)
        console.log(JSON.parse(data))
        console.log(startTime)
        if (self.currentDeviceMAC === mac) {
          let Data = JSON.parse(data)
          console.log(Data)
          Data.forEach((element, index) => {
            if (index < 7) {
              self.powerData.push(element.e)
              console.log(element.e / 1000)
              self.costData.push(element.s)
              self.lastWeekPower += (element.e)
              console.log(self.lastWeekPower)
            } else {
              console.log(element.e / 1000)
              self.thisWeekPower += (element.e)
              console.log(self.thisWeekPower)
            }
          })
          self.thisWeekPower = parseFloat(self.thisWeekPower).toFixed(2)
          self.lastWeekPower = parseFloat(self.lastWeekPower).toFixed(2)
          if (self.lastWeekPower <= 0) {
            self.dataShow = true
          }
          if (self.lastWeekPower <= 0) {
            self.powerData = []
            self.costData = []
          }
        }
        self.echart_init()
      }
      break
  }
}



