const errorLog = {
  state: {
    errorMsg: '', // 响应错误信息
    errorCode: '' // 错误码
  },
  mutations: {
    ALERT_ERROR_MSG: (state, txt) => {
      state.errorMsg = txt
    },
    SET_ERROR_CODE: (state, val) => {
      state.errorCode = val
    }
  },
  actions: {
    alertErrorMsg ({commit}, txt) {
      commit('ALERT_ERROR_MSG', txt)
    },
    setErrorCode ({commit}, val) {
      commit('SET_ERROR_CODE', val)
    }
  }
}

export default errorLog



