const user = {
  state: {
    name: 'Tom',
    lastName: 'Curise',
    tittle: null, // rooms page's head tittle
    addeivePath: null, // add devives 入口 设备列表或房间
    currentMac: null, // 当前设备mac
    currencyText: 'GBP', // 货币单位
    rate: '0.00', // 汇率
    userheadpic: null, // 头像
    roomdata: [], // 房间列表
    deviceList: [], // 设备列表 Me page显示
    devicesData: [], // 连接设备数据
    currentRouter: '',
    roomIcons: [], // 房间设备图标
    deviceName: 'Rename',
    loginStatus: false,
    maqttState: false,
    firstdevicePage: true
  },
  mutations: {
    USER_NAME: (state, name) => {
      state.name = name
    },
    SUR_NAME: (state, name) => {
      state.lastName = name
    },
    ROOM_NAME: (state, name) => {
      state.tittle = name
    },
    ADDEVICE_PATH: (state, str) => {
      state.addeivePath = str
    },
    SET_CURRENTMAC: (state, str) => {
      state.currentMac = str
    },
    SET_CURRENCY: (state, str) => {
      state.currencyText = str
      console.log(state.currencyText)
    },
    SET_Rate: (state, str) => {
      state.rate = str
    },
    SET_HEADPIC: (state, str) => {
      state.userheadpic = str
    },
    SET_ROOMDATA: (state, obj) => {
      state.roomdata = obj
    },
    SET_DEVICELIST: (state, obj) => {
      state.deviceList = obj
    },
    SET_DEVICESDATA: (state, obj) => {
      state.devicesData = obj
    }
  },
  actions: {
    userName ({ commit }, name) {
      commit('USER_NAME', name)
    },
    surName ({ commit }, name) {
      commit('SUR_NAME', name)
    },
    roomName ({ commit }, name) {
      commit('ROOM_NAME', name)
    },
    addevicePath ({ commit }, str) {
      commit('ADDEVICE_PATH', str)
    },
    setCurrentMac ({ commit }, str) {
      commit('SET_CURRENTMAC', str)
    },
    setCurrency ({ commit }, str) {
      commit('SET_CURRENCY', str)
    },
    setRate ({ commit }, str) {
      commit('SET_Rate', str)
    },
    setHeadpic ({ commit }, str) {
      commit('SET_HEADPIC', str)
    },
    setRoomdata ({commit}, obj) {
      commit('SET_ROOMDATA', obj)
    },
    setDevicelist ({commit}, obj) {
      commit('SET_DEVICELIST', obj)
    },
    setDevicesdata ({commit}, obj) {
      commit('SET_DEVICESDATA', obj)
    }
  },
  getters: {
    roomName (state) {
      return state.tittle
    },
    addevicePath (state) {
      return state.addeivePath
    },
    getuserName (state) {
      return state.name
    },
    getsurName (state) {
      return state.lastName
    },
    getcurrentMac (state) {
      return state.currentMac
    },
    getcurrency (state) {
      return state.currencyText
    },
    getrate (state) {
      return state.rate
    },
    getheadpic (state) {
      return state.userheadpic
    },
    getRoomdata (state) {
      return state.roomdata
    },
    getDevicelist (state) {
      return state.deviceList
    }
  }
}

export default user



