<template>
  <div id="app" :style="{height:fullHeight + 'px'}">
    <transition :name="transitionName" :mode="mode">
      <router-view></router-view>
    </transition>
    <loading-alert></loading-alert>
    <!-- 吐司 -->
    <error-code></error-code>
  </div>
</template>

<script>
import {
  disableGoBackRequest,
  isLoginRequest,
  setStatusBarRequest
} from './api/method'
import {
  goBackResponse,
  // switchNetworkResponse,
  errorCodeResponse,
  isLoginResponse,
  networkStatusResponse
} from './api/getData'
// import toast from './components/common/toast'
import errorCode from './components/errorCode/index'
import loadingAlert from './components/Loading/index'
// import {setStore} from './utils/index'
export default {
  name: 'App',
  data () {
    return {
      fullHeight: null, // 屏幕高度
      transitionName: 'fade',
      mode: 'in-out'
    }
  },
  components: {
    errorCode,
    loadingAlert
  },
  computed: {
  },
  mounted () {
    this.fullHeight = document.documentElement.clientHeight
    // 禁用原生返回
    disableGoBackRequest(this.NAVIGATOR, this, true)
    // 状态栏隐藏
    setStatusBarRequest(this.NAVIGATOR, this, false, '')
    // 返回操作
    goBackResponse(this.NAVIGATOR, this)
    // 手机连接网络切换返回
    // switchNetworkResponse(this.NAVIGATOR, this)
    // 后台返回错误码 Error
    errorCodeResponse(this.NAVIGATOR, this)
    // // 网络状态返回
    networkStatusResponse(this.NAVIGATOR, this)
    if (this.$root.ISDEBUG) {
      // window.errorCodeResponse('0x802501')
    }
    // 检验是否登录
    isLoginRequest(this.NAVIGATOR, this)
    isLoginResponse(this.NAVIGATOR, this)
    // if (this.$root.ISDEBUG) {
    //   setTimeout(() => {
    //     window.isLoginResponse(true)
    //     clearTimeout()
    //   }, 2000)
    // }
    // setStore('devicesData', '')
    // let stroage = window.localStorage
    // stroage.removeItem('devicesData')
  },
  watch: {
    $route (to, from) {
      this.$store.state.user.currentRouter = to.name
      console.log(to.name)
      console.log(from.name)
      if (to.name === 'login') {
        this.transitionName = 'fade'
      }
      if (from.name === 'login') {
        this.transitionName = 'slide-r'
        this.mode = 'out-in'
      }
      // else if (to.name === 'me' && from.name === 'temperature') {
      //   this.transitionName = 'slide-right'
      // } else if (to.name === 'configwifi' && from.name === 'configureFailed') {
      //   this.$store.state.backdre = false
      // } else {
      //   this.transitionName = 'fade'
      // }
    }
  }
}
</script>

<style lang="less">
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}
</style>



